"use client"

import { createContext, useContext, useState, useEffect, type ReactNode } from "react"
import { type User, mockUsers } from "@/lib/mock-data"

interface AuthContextType {
  user: User | null
  login: (userId: number) => boolean
  logout: () => void
  isLoading: boolean
}

const AuthContext = createContext<AuthContextType | undefined>(undefined)

export function AuthProvider({ children }: { children: ReactNode }) {
  const [user, setUser] = useState<User | null>(null)
  const [isLoading, setIsLoading] = useState(true)

  // Load user from localStorage on mount
  useEffect(() => {
    const storedUserId = localStorage.getItem("movieRec_userId")
    if (storedUserId) {
      const foundUser = mockUsers.find((u) => u.id === Number.parseInt(storedUserId))
      if (foundUser) {
        setUser(foundUser)
      }
    }
    setIsLoading(false)
  }, [])

  const login = (userId: number): boolean => {
    const foundUser = mockUsers.find((u) => u.id === userId)
    if (foundUser) {
      setUser(foundUser)
      localStorage.setItem("movieRec_userId", userId.toString())
      return true
    }
    return false
  }

  const logout = () => {
    setUser(null)
    localStorage.removeItem("movieRec_userId")
  }

  return <AuthContext.Provider value={{ user, login, logout, isLoading }}>{children}</AuthContext.Provider>
}

export function useAuth() {
  const context = useContext(AuthContext)
  if (context === undefined) {
    throw new Error("useAuth must be used within an AuthProvider")
  }
  return context
}
