"use client"

import type React from "react"

import { useState } from "react"
import { useAuth } from "@/contexts/auth-context"
import { mockUsers } from "@/lib/mock-data"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { LogIn, User } from "lucide-react"

interface LoginDialogProps {
  trigger?: React.ReactNode
}

export function LoginDialog({ trigger }: LoginDialogProps) {
  const { login } = useAuth()
  const [userId, setUserId] = useState("")
  const [error, setError] = useState("")
  const [open, setOpen] = useState(false)

  const handleLogin = () => {
    const id = Number.parseInt(userId)
    if (isNaN(id)) {
      setError("Vui lòng nhập User ID hợp lệ")
      return
    }

    const success = login(id)
    if (success) {
      setOpen(false)
      setUserId("")
      setError("")
    } else {
      setError("Không tìm thấy User ID này trong hệ thống")
    }
  }

  const handleQuickLogin = (id: number) => {
    login(id)
    setOpen(false)
    setUserId("")
    setError("")
  }

  return (
    <Dialog open={open} onOpenChange={setOpen}>
      <DialogTrigger asChild>
        {trigger || (
          <Button variant="outline" size="sm" className="gap-2 bg-transparent">
            <LogIn className="h-4 w-4" />
            Đăng nhập
          </Button>
        )}
      </DialogTrigger>
      <DialogContent className="sm:max-w-md">
        <DialogHeader>
          <DialogTitle>Đăng nhập</DialogTitle>
          <DialogDescription>Nhập User ID của bạn để đăng nhập vào hệ thống gợi ý phim</DialogDescription>
        </DialogHeader>

        <div className="space-y-4 py-4">
          {/* Manual Input */}
          <div className="space-y-2">
            <Label htmlFor="userId">User ID</Label>
            <div className="flex gap-2">
              <Input
                id="userId"
                type="number"
                placeholder="Nhập User ID..."
                value={userId}
                onChange={(e) => {
                  setUserId(e.target.value)
                  setError("")
                }}
                onKeyDown={(e) => {
                  if (e.key === "Enter") handleLogin()
                }}
              />
              <Button onClick={handleLogin}>Đăng nhập</Button>
            </div>
            {error && <p className="text-sm text-destructive">{error}</p>}
          </div>

          {/* Divider */}
          <div className="relative">
            <div className="absolute inset-0 flex items-center">
              <span className="w-full border-t" />
            </div>
            <div className="relative flex justify-center text-xs uppercase">
              <span className="bg-background px-2 text-muted-foreground">Hoặc chọn nhanh</span>
            </div>
          </div>

          {/* Quick Select Users */}
          <div className="grid grid-cols-1 gap-2">
            {mockUsers.map((user) => (
              <Button
                key={user.id}
                variant="outline"
                className="justify-start gap-3 bg-transparent"
                onClick={() => handleQuickLogin(user.id)}
              >
                <div className="flex h-8 w-8 items-center justify-center rounded-full bg-primary/10">
                  <User className="h-4 w-4 text-primary" />
                </div>
                <div className="text-left">
                  <p className="font-medium">{user.name}</p>
                  <p className="text-xs text-muted-foreground">ID: {user.id}</p>
                </div>
              </Button>
            ))}
          </div>
        </div>
      </DialogContent>
    </Dialog>
  )
}
